<template>
    <v-app>
      <v-container class="mt-5">
        <h1 class="text-center mx-6 my-6 mt-1 text-h2 Italic text purple lighten-4 fluid">Peliculas de comedia</h1>
        <v-row>
          <v-col v-for="(p, i) in generos" :key="i">
            <v-card class="mx-auto" max-width="344">
              <v-img :src="p.poster" height="200px"></v-img>
              <v-card-title>
                {{ p.titulo}}
              </v-card-title>
              <v-card-actions>
                <v-btn color="orange lighten-2" text>
                  Explore
                </v-btn>
                <v-spacer></v-spacer>
  
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-app>
  
  </template>
  <script>
  import axios from 'axios';
  export default {
    name: "PageComedia",
    data() {
      return {
        generos: [],
  
  
  
  
      }
    },
    methods: {
      traer() {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .get("https://proyectmovie.herokuapp.com/api/pelicula", header)
  
          .then((response) => {
            this.generos = response.data.peliculas.filter((e) => e.genero === "Comedia")
  
  
            console.log(this.generos)
          })
          .catch((error) => {
            console.log(error);
          });
      },
  
      //   computed: {
  
      //     clasificarAnimacion() {
      //      console.log('camilo')
      //     return this.generos.filter((e)=>e.genero === "Animacion")
      //   }
      // },
    },
    created() {
      this.traer();
      //this.clasificarAnimacion()
  
    },
  
  }
  </script>