import Vue from "vue";
import Vuex from "vuex";


Vue.use(Vuex);

export const store = new Vuex.Store({
    state:{
        token:"",
        datos:{},
        action:[],
        peliculas:[],
        personas:{},
        foto:[],
    
        reparto:[],
        agregarFavorito:[],
        desserts:[],
        generos:[],
        favMovies:[],
        actore:[],
        actor:[]
        


    },

    mutations:{
        setToken(state, value){
            state.token=value
        },
        setDatos(state, value){
            state.datos=value
        },
        setPelicula(state,v){
            state.peliculas=v
        },
        setGeneros(state, v){
            state.generos=v
        },
        setMovie(state,v){
            state.movies=v
        },
        setDatosUsuario(state,v){
            state.personas=v
        },
        setAction(state,v){
            state.action.push(v);
        },
        setReparto(state, value){
            state.reparto.push(value)
        },
        setAgregarFavorito(state,v){
            state.agregarFavorito.push(v)
        },
        setFavMovies(state, movie){
            state.setFavMovies.push(movie) 
        }
       
        
        
    },
    actions:{
        setToken(context, value){
            context.commit("setToken", value)
        },
        setDatos(context,value){
            context.commit("setDatos", value)
        },
        setPeliculas(context,value){
            context.commit("setPeliculas",value)
        },
        setGeneros(context,value){
            context.commit("setGeneros",value)
        },
        setMovies(context,value){
            context.commit("setMovies",value)
        },
        setDatosUsuario(context,value){
            context.commit("setDatosUsuario",value)
        },
        setReparto(context,value){
            context.commit("setReparto",value)
        },
        setLogout(context,value){
            context.commit("setlogout", value)
        }
        

    }

    
})