<template>
  <v-app>
    <v-container fluid>
      <v-card>
        <v-card-title>
          Eliminar Pelicula
          <v-spacer></v-spacer>
          <v-text-field v-model="search" type="search" append-icon="mdi-magnify" label="Search" single-line hide-details  @keyup="buscar(id)">
          </v-text-field>
        </v-card-title>
        <v-simple-table class="elevation-1 mx-5" outlined color="black" :headers="headers" :items="desserts" :search="search">

          <template v-slot:default>
            <thead>
              <tr class="purple lighten-4">
                <th class="text-center">
                  ID
                </th>
                <th class="text-center">
                  Titulo
                </th>
                <th class="text-center">
                  Duracion
                </th>
                <th class="text-center">
                  Poster
                </th>
                <th class="text-center">
                  Genero
                </th>
                <th class="text-center">
                  Sinopsis
                </th>
                <th class="text-center">
                  Estado
                </th>
                <th class="text-center">
                  Idioma
                </th>
                <th class="text-center">
                  Director
                </th>
                <th class="text-center">
                  Reparto
                </th>
                <th class="text-center">
                  Eliminar
                </th>



              </tr>
            </thead>
            <tbody>
              <tr v-for="item in desserts" :key="item._id" outlined color="black">
                <td outlined class="purple lighten-5">{{ item._id}}</td>
                <td outlined color="black" class="purple lighten-5">{{ item.titulo}}</td>
                <td class="purple lighten-5">{{ item.duracion }}</td>
                <td class="purple lighten-5">{{ item.poster }}</td>
                <td class="purple lighten-5">{{ item.genero }}</td>
                <td class="purple lighten-5">{{ item.sinopsis }}</td>
                <td class="purple lighten-5">{{ item.estado }}</td>
                <td class="purple lighten-5">{{ item.idioma }}</td>
                <td class="purple lighten-5">{{ item.director}}</td>
                <td class="purple lighten-5">{{ item.reparto }}</td>

                <td class="purple lighten-4">
                  <v-btn @click="deleteItem(item._id)">❌</v-btn>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>

      </v-card>




    </v-container>
  </v-app>

</template>
<script>
import axios from 'axios';
export default {

  name: "PageEliminar",
  data() {
    return {
      dialog: false,
      dialogDelete: false,
      search:"",
      headers: [

        { text: 'ID', value: 'id' },
        { text: 'Titulo', value: 'titulo' },
        { text: 'Duracion', value: 'duracion' },
        { text: 'Poster', value: 'poster' },
        { text: 'Genero', value: 'genero' },
        { text: 'Sinopsis', value: 'sinopsis' },
        { text: 'Estado', value: 'estado' },
        { text: 'Idioma', value: 'idioma' },
        { text: 'Director', value: 'director' },
        // { text: 'Reparto Actor', value: 'reparto' },
        // { text: 'Personaje', value: 'Personaje' },
        // { text: 'Rol', value: 'rol' },
        { text: 'Actions', value: 'actions' },
      ],
      desserts: [],
      buscarPelicula:{},
     



    }
  },
 
  methods: {
   
    traerPeli() {
      let header = { headers: { "x-token": this.$store.state.token } };

      axios
        .get("https://proyectmovie.herokuapp.com/api/pelicula", header)

        .then((response) => {
          console.log(response);
          this.desserts = response.data.peliculas;

        })
        .catch((error) => {
          console.log(error);
        });
    },
    buscar(id) {
      
      id=this.search
      
      axios
          .get(`https://proyectmovie.herokuapp.com/api/pelicula/id/${id}`)

          .then((response) => {
            console.log(response);
            this.buscarPelicula = response.data.pelicula
            console.log(this.buscarPelicula)
          })
          .catch((error) => {
            console.log(error);
          });
        
      
      // let nombre = this.search
     
      //   axios.get(`https://proyectmovie.herokuapp.com/api/pelicula/id/${nombre}`)
      //   .then((response)=>{
      //     console.log(response)
      //     this.buscarPelicula=response.data.pelicula
      //   })
      //   .catch((error)=>{
      //     console.log(error)
      //   })
   
  },
    deleteItem(id) {
      if (id) {
        axios
          .delete(`https://proyectmovie.herokuapp.com/api/pelicula/${id}`)
          .then((response) => {

            console.log(response);

          })
          .catch((error) => {
            console.log(error);
          });

      }
    },
   


  },
  created() {
    this.traerPeli();
    this.buscar()

  },
}
</script>
