<template>
  <v-app>
    <v-container>
      <div>
        <v-alert 
        outlined
      type="red lighten-2"
      prominent
      border="left"
      v-show="alert"
    >
     La informacion ingresada es incorrecta, intenta de nuevo
    </v-alert>

      </div>
      
      <v-row>
        <v-col justify="center" class="mt-15">
          <v-card class="mx-auto elevation-24 text-center purple lighten-5 text-no-wrap rounded-lg" max-width="600">
            <v-system-bar color="purple lighten-4" dark>

            </v-system-bar>
            <v-toolbar color="purple lighten-3 text-center" dark>
              <v-toolbar-title class="text-center">Validacion de usuario</v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-system-bar class="purple lighten-4">
              <p class="mt-3">Al dar click en Ingresar, aceptas los terminos y condiciones de repelis</p>
            </v-system-bar>
            <v-form ref="form"   class="mt-5">
              <v-text-field v-model="email" label="Email" required outlined class="mx-4">
              </v-text-field>
              <v-text-field v-model="password" :counter="10" type="password" label="Password" required outlined
                class="mx-4"></v-text-field>
              <h4 class="text-center"> ¿No tienes cuenta aún? <router-link to="/registro"> registrarme </router-link>
              </h4>
              <h4 class="text-center"> ¿Olvidaste la contraseña?</h4>

              <v-btn class="purple lighten-3 text-no-wrap rounded-pill text-center my-5" @click="login()">
                Ingresar
              </v-btn>

            </v-form>

          </v-card>

        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
import axios from "axios";
export default {
  name: "PageLogin",

  data: () => ({
    email: "",
    password: "",
    alert: false,

  }),
  methods: {
    login() {
      axios.post(
        "https://proyectmovie.herokuapp.com/api/persona/login/user", {
        email: this.email,
        password: this.password
      })
        .then((response) => {
          console.log(response);
          this.$store.dispatch("setToken", response.data.token)
          this.$store.dispatch("setDatosUsuario", response.data.persona);
          this.$router.push("/usuario");
        })
        .catch((error) => {
          console.log(error);
          
        return this.alert = true
      

        });
    },
    // validacion(){
    //   if (this.email === "" ){
    //     return this.alert = true
    //   }
    // }

  },
};
</script>
<style>
.imagen {
  background-image: url(https://as01.epimg.net/meristation/imagenes/2022/01/15/noticias/1642238725_301426_1642238820_noticia_normal.jpg);
  height: 1000px;
  background-repeat: repeat;
}

.login {
  border: 1px;
  border-radius: 5px;
  padding: 100px;

}
</style>
