<template>
  <v-app>
    <v-container>
      <v-row>
        <v-col>
          <v-card elevation="24" max-width="1200" class="mx-auto purple lighten-4">
            <v-system-bar lights-out></v-system-bar>
            <v-carousel :continuous="false" :show-arrows="false" hide-delimiter-background delimiter-icon="mdi-minus"
              height="300">
              <v-carousel-item
                src="https://image.shutterstock.com/image-vector/realistic-cinema-advertising-poster-popcorn-260nw-1411841174.jpg"
                reverse-transition="fade-transition" transition="fade-transition">
              </v-carousel-item>
            </v-carousel>
            <v-list two-line>
              <v-list-item>
                <v-list-item-avatar class="ml-4 mt-2 my-16">
                  <v-file-input width="20" accept="image/png, image/jpeg, image/bmp" prepend-icon="mdi-camera"
                    v-model="poster" @change="subirFoto(persona._id)"></v-file-input>
                </v-list-item-avatar>
                <v-list-item-avatar size="80" class="ml-n9">
                  <v-img justify="center" :src="persona.poster" height="100" width="20"></v-img>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>
                    <h2 class="ml-8 font-weight-thin font-weight-black"> {{ persona.nombre }} {{ persona.apellido }}
                    </h2>
                  </v-list-item-title>
                  <v-list-item-subtitle></v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-card>
        </v-col>
      </v-row>
      <v-sheet class="d-flex mt-6" color="purple lighten-4" height="20">
        <p class="font-weight-black font-italic">Continuar viendo </p>
      </v-sheet>
      <v-row>
        <v-col class="mt-6" v-for="(p, i) in movie" :key="i">
          <v-card class="mx-auto" max-width="400">
            <v-img class="white--text align-end" height="200px" :src="p.poster">
              <v-card-title class="font-weight-black font-italic">{{ p.titulo }}</v-card-title>
            </v-img>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn icon>
                <v-icon>mdi-heart</v-icon>
              </v-btn>
              <v-btn icon>
                <v-icon>mdi-bookmark</v-icon>
              </v-btn>
              <v-btn icon>
                <v-icon>mdi-share-variant</v-icon>
              </v-btn>
              <v-btn text color="deep-purple lighten-3 " @click="detalle(p) ">Mas Detalles </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>
<script>
import axios from 'axios'
export default {
  name: "PageUsuario",
  data: () => ({
    show:"true",
    continuous:"true",
    movie: [],
    persona: {},
    poster: ""

  }),
  methods: {
    traerPeli() {
      let header = { headers: { "x-token": this.$store.state.token } };

      axios
        .get("https://proyectmovie.herokuapp.com/api/pelicula", header)

        .then((response) => {
          console.log(response);
          this.movie = response.data.peliculas;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    traerUsuario() {
      this.persona = this.$store.state.personas
      console.log(this.persona)

    },
    subirFoto(id) {
      console.log(id);
      let fd = new FormData();
      fd.append("archivo", this.poster);
      console.log(fd);
      axios
        .post(
          `https://proyectmovie.herokuapp.com/api/persona/subir/${id}`,
          fd,
        )
        .then((response) => {
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });

    },
    detalle(p) {
      this.$router.push("/pelicula");
      this.$store.dispatch("setDatos", p);
      console.log(this.$store.state.datos);
    },
  },
  created() {
    this.traerPeli();
    this.traerUsuario();
  },
}
</script>