<template>
  <v-app>
    <v-container class="mt-16" fluid>
      <v-row>
        <v-col cols="6">
          <v-card class="purple lighten-3" height="auto">
            <v-img :src="detallePelicula.poster" max-height="350" contain class="purple lighten-4"></v-img>
            <v-list-item-avatar class="ml-4 mt-2">
              <v-file-input width="20" accept="image/png, image/jpeg, image/bmp" prepend-icon="mdi-camera"
                v-model="poster" @change="subirFoto(detallePelicula._id)" v-if="$store.state.token != ''">
              </v-file-input>
            </v-list-item-avatar>
            <v-card-title class="text-h4 justify-center font-weight-black font-italic ">
              {{ this.detallePelicula.titulo }}
            </v-card-title>
          </v-card>
        </v-col>

        <v-col cols="6" class="float-xl-none">
          <v-card height="auto" width="600" class="purple lighten-5 mt-8">
            <v-card-text class="font-weight-black purple lighten-3">{{ detallePelicula.sinopsis}} </v-card-text>
            <p class="mt-5 ml-8"> Genero: {{ detallePelicula.genero }} </p>
            <p class="ml-8">Idioma: {{ detallePelicula.idioma }} </p>
            <p class="ml-8">Duracion: {{ detallePelicula.duracion }} </p>
            <p class="ml-8">Estado: {{ detallePelicula.estado}} </p>
            <p class="ml-8">Director: {{ detallePelicula.director }} </p>

            <v-card-action class="justify-center ">
              <v-btn v-if="$store.state.token != ''" rounded color="black" class="purple lighten-4 ml-16 mt-5 my-5">
                Ver Pelicula
              </v-btn>
              <v-btn icon color="pink" class="mt-5 my-5" v-if="$store.state.token != ''" @click="agregarFavorito()">
                <v-icon>mdi-heart</v-icon>
              </v-btn>

            </v-card-action>


          </v-card>

          
      
              


        </v-col>
      </v-row>
    
<v-row>
  <v-col v-for="(p, i) in actor" :key="i">
    <v-card class="mx-auto mt-6" max-width="300">
      <v-img :src="actore.foto" height="300px"></v-img>
      <v-card-title>
        {{p.personaje}}
      </v-card-title>
      <v-card-subtitle>
        {{p.rol}}
      </v-card-subtitle>


    </v-card>



  </v-col>
</v-row>

      





      
    </v-container>
  </v-app>
</template>
<script>
import axios from 'axios'
export default {
  name: "PagePelicula",
  data() {
    return {
      detallePelicula: [],
      poster: "",
      actor: [],
      reparto: [],
      actore: [],
      personaje: "",
      rol: "",
      id: "",
    

    };
  },
  methods: {
    traerPeli() {
      this.detallePelicula = this.$store.state.datos;
      this.actor = this.$store.state.datos.reparto;
      console.log(this.actor)

    },
    subirFoto(id) {
      console.log(id);
      let ft = new FormData();
      ft.append("archivo", this.poster);
      console.log(ft);
      axios
        .post(
          `https://proyectmovie.herokuapp.com/api/pelicula/subir/${id}`,
          ft,
        )
        .then((response) => {
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });

    },
    agregarFavorito() {
      axios.post("https://proyectmovie.herokuapp.com/api/favorito", {
        idPelicula: this.detallePelicula._id,
        poster: this.detallePelicula.poster,
        titulo: this.detallePelicula.titulo,
        genero: this.detallePelicula.genero
      })
        .then((response) => {
          console.log(response.data.favoritos)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    
    traerActor(id) {
      id = this.actor[0].idActor
      console.log(id)
      axios
        .get(`https://proyectmovie.herokuapp.com/api/actor/mostrar/id/${id}`)
        .then((response) => {
          this.actore = response.data.actor
          console.log(this.actore)
        })
        .catch((err) => {
          console.log(err)
        })
    },

  },

  created() {
    this.traerPeli();
    this.traerActor();
   

  },

};
</script>

