<template>
  <v-app>
    <v-container class="mt-16">
      <h1 class="text-center mx-6 my-6 mt-1 text-h2 Italic text purple lighten-4 fluid">Mis peliculas favoritas</h1>
      <v-row>
        <v-col v-for="(p, i) in agregarFavorito" :key="i" class="text-center">
          <v-card class="mx-auto" max-width="344">
            <v-img :src="p.poster" height="200px"></v-img>
            <v-card-subtitle class="text-h3 my-1">
              {{ p.titulo }}
            </v-card-subtitle>
            <v-card-text class="mt-1"> Genero:
              {{ p.genero }}
            </v-card-text>
            <v-btn justify="center" rounded color="purple lighten-3 mt-1 my-2" @click="deleteItem(p._id)">Eliminar
            </v-btn>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>
<script>
import axios from 'axios'
export default {
  name: "PageFavorita",
  data() {
    return {
      agregarFavorito: []
    }
  },
  methods: {
    traerfavorita() {

      axios
        .get("https://proyectmovie.herokuapp.com/api/favorito")

        .then((response) => {
          this.agregarFavorito = response.data.favorito
          console.log(this.agregarFavorito)
        })
        .catch((error) => {
          console.log(error);
        });
    },
    deleteItem(id) {
      if (id) {
        axios
          .delete(`https://proyectmovie.herokuapp.com/api/favorito/${id}`)
          .then((response) => {
            console.log(response);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },
  created() {
    this.traerfavorita()
  }
}
</script>