  <script>
    import axios from 'axios';
   export default {
  
  name: "PageEditar",
  data() {
    return {
      dialog: false,
        dialogDelete: false,
 
          titulo:"",
          duracion:"",
          gereno: "",
          sinopsis: "",
          estado:"",
          idioma:"",
          director:"",
          desserts:[],

          search:"",
          page:1,
         
         
         
      
       
        
    }
  },
  methods: {
    traerPeli(id){
      console.log(id)
       id=this.search
      
      axios
          .get(`https://proyectmovie.herokuapp.com/api/pelicula/id/${id}`)
  
          .then((response) => {
            console.log(response);
            this.desserts = response.data.pelicula;
          })
          .catch((error) => {
            console.log(error);
          });
      },
    
    realizarBusqueda(){
      this.page =1;
      this.traerPeli();
    },
    
    editarItem(id){
      id=this.search
        axios
        .put(`https://proyectmovie.herokuapp.com/api/pelicula/${id}`,{
           titulo: this.desserts.titulo,
          duracion: this.desserts.duracion,
          genero: this.desserts.genero,
          sinopsis: this.desserts.sinopsis,
          estado: this.desserts.estado,
          idioma: this.desserts.idioma,
          director: this.desserts.director,
         
        }
         )
        .then((response) => {
          
            console.log(response);
            
          })
          .catch((error) => {
            console.log(error);
          });
  
      
    },
     
   
  },
  created() {
    this.page =1;
      this.traerPeli();
      this.editarItem()
     
      
    },
  }
  </script>